import React, { Component } from "react";
import { Fade, Slide } from "react-reveal";

class Contact extends Component {

  render() {
    if (!this.props.data) return null;

    const name = this.props.data.name;
    const street = this.props.data.address.street;
    const city = this.props.data.address.city;
    const state = this.props.data.address.state;
    const zip = this.props.data.address.zip;
    const phone = this.props.data.phone;
    const message = this.props.data.contactmessage;

    return (
      <section id="contact">
        <Fade bottom duration={1000}>
          <div className="row section-head">
            <div className="two columns header-col">
              <h1>
                <span>Get In Touch.</span>
              </h1>
            </div>

            <div className="ten columns">
              <p className="lead">{message}</p>
            </div>
          </div>
        </Fade>

        <div className="row">
          <Slide left duration={1000}>
            <div className="eight columns">
              <form target="hidden_iframe" action="https://docs.google.com/forms/d/e/1FAIpQLSd1YmM2T_xUxKuSpCgeVSK7r_ZaGL7KA_47R_nyiM7GdO9vQA/formResponse?" method="post" id="contactForm" name="contactForm">
                <fieldset>
                  <div>
                    <label htmlFor="contactName">
                      Name <span className="required">*</span>
                    </label>
                    <input
                      type="text"
                      defaultValue=""
                      size="35"
                      id="entry.1703954431"
                      name="entry.1703954431"
                      onChange={this.handleChange}
                    />
                  </div>

                  <div>
                    <label htmlFor="contactEmail">
                      Email <span className="required">*</span>
                    </label>
                    <input
                      type="text"
                      defaultValue=""
                      size="35"
                      id="entry.1396502853"
                      name="entry.1396502853"
                      onChange={this.handleChange}
                    />
                  </div>

                  <div>
                    <label htmlFor="contactSubject">Subject</label>
                    <input
                      type="text"
                      defaultValue=""
                      size="35"
                      id="entry.114815141"
                      name="entry.114815141"
                      onChange={this.handleChange}
                    />
                  </div>

                  <div>
                    <label htmlFor="contactMessage">
                      Message <span className="required">*</span>
                    </label>
                    <textarea
                      cols="50"
                      rows="15"
                      id="entry.234805479"
                      name="entry.234805479"
                    ></textarea>
                  </div>

                  <div>
                    <button className="submit">Submit</button>
                  </div>
                </fieldset>
              </form>

              <iframe name="hidden_iframe" id="hidden_iframe" style={{display: 'none'}} onload="if(submitted) {}"></iframe>
            </div>
          </Slide>

          <Slide right duration={1000}>
            <aside className="four columns footer-widgets">
              <div className="widget widget_contact">
                <h4>Contact</h4>
                <p className="address">
                  {name}<br/>
                  {"sidharthmrao@gmail.com"}
                  {street} <br />
                  {city}, {state} {zip}
                  <br />
                  <span>{phone}</span>
                </p>
              </div>

              <div className="widget widget_tweets">
                <h4 className="widget-title">My Friends' Websites:</h4>
                <ul id="twitter">
                  <li>
                    <span>
                      <a href="https://ryanyang.us">Ryan Yang</a>
                    </span>
                  </li>
                  <li>
                    <span>
                      <a href="https://www.korvyakov.us">Michael Korvyakov</a>
                    </span>
                  </li>
                  <li>
                    <span>
                      <a href="https://tsviii.wixsite.com/david">David Garsten</a>
                    </span>
                  </li>
                </ul>
              </div>
            </aside>
          </Slide>
        </div>
      </section>
    );
  }
}

export default Contact;
